@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/ptsans.woff2"), url("../../fonts/ptsans.woff");
}
@font-face {
  font-family: "PT Sans";
  font-display: swap;
  font-weight: 700;
  font-style: normal;
  src: url("../../fonts/ptsansbold.woff2"), url("../../fonts/ptsansbold.woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background-color: #ffffff;
  text-transform: uppercase;
  position: relative;
  min-height: 56px;
}

.header__logo {
  display: flex;
  justify-content: center;
  z-index: 2;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media (min-width: 768px) {
  .header {
    position: relative;
  }

  .header__logo {
    display: block;
    justify-content: unset;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
  }
}
.page {
  height: 100%;
  background-color: #E7E5E5;
}

.page__wrapper {
  min-height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", Arial, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .page__wrapper {
    margin: auto;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.2);
  }
}
@media (min-width: 1200px) {
  .page__wrapper {
    margin: auto;
    max-width: 1200px;
    box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.2);
  }
}
.main-nav {
  background-color: #81b3d2;
}

.main-nav__toggle {
  position: absolute;
  top: 17px;
  right: 20px;
  z-index: 2;
  width: 23px;
  height: 23px;
  cursor: pointer;
  border: none;
  background-color: transparent;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 8px 0 0 #81b3d2, 0 16px 0 0 #81b3d2;
}
.main-nav--closed .main-nav__toggle:focus::before {
  background-color: rgba(129, 179, 210, 0.6);
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 5px;
  width: 30px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:focus::before, .main-nav--opened .main-nav__toggle:focus::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav--opened .main-nav__toggle:active::before, .main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
}

.main-nav__item {
  text-align: center;
  padding: 17px 0 12px 0;
}

.main-nav__link {
  text-decoration: none;
  color: #ffffff;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

@media (max-width: 767px) {
  .main-nav {
    z-index: 3;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  .main-nav__item {
    width: 100%;
    border-bottom: solid 1px rgba(255, 255, 255, 0.2);
    border-top: solid 1px rgba(1, 1, 1, 0.2);
  }
  .main-nav__item:first-child {
    border-top: none;
  }
  .main-nav__item:last-child {
    border-bottom: none;
    position: relative;
  }
  .main-nav__item:last-child::after {
    content: "";
    position: absolute;
    bottom: 0px;
    right: 0px;
    width: 100%;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
  .main-nav__item:hover, .main-nav__item:focus {
    background-color: #669ec0;
  }
  .main-nav__item:active {
    background-color: #5496BD;
  }
  .main-nav__item:active .main-nav__link {
    opacity: 0.3;
  }

  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    flex-direction: row;
  }

  .main-nav__item:nth-child(3) {
    margin-left: auto;
  }

  .main-nav__item {
    padding: 18px 28px;
    border: none;
  }

  .main-nav__item:first-child {
    padding: 18px 47px;
  }

  .main-nav__link {
    font-size: 14px;
    line-height: 21px;
    color: #000000;
  }

  .main-nav__link:hover, .main-nav__link:focus {
    color: #81b3d2;
  }
  .main-nav__link:active {
    color: rgba(0, 0, 0, 0.3);
  }

  .main-nav__link--current,
.main-nav__link--current:hover {
    color: #766357;
  }
}
@media (min-width: 768px) and (min-width: 1200px) {
  .main-nav__item {
    padding: 18px 70px;
  }

  .main-nav__item:first-child {
    padding: 18px 91px;
  }
}
